import React, { Component } from 'react';

class OurFeatures extends Component {
    render() {
        return (
            <section className="features-area ptb-100 bg-image">
                <div className="container">
                    <div className="section-title">
                        <span>Sản Phẩm</span>
                        <h2>Chúng tôi luôn cố gắng để <b>Hiểu rõ</b> Mong muốn của bạn</h2>
                        <p>Để trở thành đối tác hàng đầu trong lĩnh vực bất động sản chungs tôi luôn mang đến giá trị tốt nhất cho khách hàng</p>
                    </div>

                    <div className="row">
                        <div className="col-lg-4 col-md-6">
                            <div className="single-features">
                                <div className="icon">
                                    <i className="icofont-edit"></i>
                                </div>
                                <h3>Sản phẩm tốt nhất</h3>
                                <p>Luôn cung cấp những sản phẩm đã được thẩm định về chất lượng, giá thành, vị trí,...</p>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-6">
                            <div className="single-features active">
                                <div className="icon">
                                    <i className="icofont-upload-alt"></i>
                                </div>
                                <h3>Khách hàng hài lòng</h3>
                                <p>Trong kinh doanh, sự hài lòng của khách hàng đối với sản phẩm hoặc dịch vụ là vô cùng quan trọng. Đây là nền móng, là điều kiện cần trong bất kỳ chiến lược giữ chân khách hàng nào của doanh nghiệp cũng như để xây dựng hệ thống khách hàng</p>
                            </div>
                        </div>

                        <div className="col-lg-4 col-md-6 offset-lg-0 offset-md-3">
                            <div className="single-features">
                                <div className="icon">
                                    <i className="icofont-pie-chart"></i>
                                </div>
                                <h3>Dễ dàng thuận tiện</h3>
                                <p>Thông tin cập nhật liên tục và đầy đủ trên hệ thống website, cập nhật thông tin đến khách hàng khi cần thiết.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default OurFeatures;